import React, { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../hooks';
import {
  Tabs,
  TabsHeader,
  TabsBody,
  Tab,
  TabPanel,
  Card,
  Typography,
  Alert,
} from '@material-tailwind/react';
import { selectMenu } from '../../features/navigationSlice';

import { setFilteredStocks } from '../../features/historicalDataSlice';
import { filterScannerResults, buildTiingoStocklist } from '../../utils';
import { useNavigate } from 'react-router-dom';
import ViewSvg from '../images/ViewSvg';
import FeedList from './FeedList';

const VOL_HEADER = ['Symbol', 'Volume'];
const GAIN_HEADER = ['Symbol', 'Volulme', 'Change'];

const LiveFeed = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const topVolume = useAppSelector((state) => state.scanner.topVolume);
  const topGainers = useAppSelector((state) => state.scanner.topGainers);
  const showFilteredStockNews = async () => {
    const symbols = buildTiingoStocklist(
      topVolume.map((stock) => stock.ticker)
    );
    dispatch(selectMenu('news'));
    navigate('/tiingo', { state: symbols });
  };
  const showGainerNews = async () => {
    const symbols = buildTiingoStocklist(
      topGainers.slice(0, 4).map((stock) => stock.symbol)
    );
    dispatch(selectMenu('news'));
    navigate('/tiingo', { state: symbols });
  };
  return (
    <>
      <div
        className="flex  hover:cursor-pointer pl-3 pt-3"
        onClick={showFilteredStockNews}
      >
        <div className="view-all-button">
          <ViewSvg />

          <span className="text ml-1">
            <span className="text-green">Current</span> News
          </span>
        </div>
      </div>
      <div
        className="flex  hover:cursor-pointer pl-3 pt-3"
        onClick={showGainerNews}
      >
        <div className="view-all-button">
          <ViewSvg />

          <span className="text ml-1">
            <span className="text-green">Gainer</span> News
          </span>
        </div>
      </div>
      <Tabs value="html">
        <Alert>Dummy Data - development currently in progress</Alert>
        <TabsHeader>
          <Tab key="Volume" value="vol">
            <h4 className="text-black"> Volume</h4>
          </Tab>
          <Tab key="Gainers" value="gain">
            <h4 className="text-black"> Gainers</h4>
          </Tab>
        </TabsHeader>
        <TabsBody
          animate={{
            initial: { y: 250 },
            mount: { y: 0 },
            unmount: { y: 250 },
          }}
        >
          <TabPanel key="Volume" value="vol">
            <Card className="h-full w-full overflow-scroll px-6">
              <FeedList stocks={topVolume} />
            </Card>
          </TabPanel>
          <TabPanel key="Gainers" value="gain">
            <Card className="h-full w-full overflow-scroll px-6">
              <FeedList stocks={topGainers} />
            </Card>
          </TabPanel>
        </TabsBody>
      </Tabs>
    </>
  );
  // return <div>{JSON.stringify(topVolume)}</div>;
};

export default LiveFeed;
